@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap'); 

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

*::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
* {
-ms-overflow-style: none;  /* IE and Edge */
scrollbar-width: none;  /* Firefox */
}

body {
  width: 100vw;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


.home_container {
  width: 100%;
  height: 100vh;
  background: rgb(16, 113, 113);
  background: linear-gradient(
    140deg,
    rgba(16, 113, 113, 1) 0%,
    rgba(9, 78, 67, 1) 100%
  );
  padding: 6em 8em;
  scroll-behavior: smooth;
  box-sizing: border-box;
  z-index: 99;
}

.navigation {
  width: 100%;
}

.home_header {
  font-family: 'Nunito', sans-serif;
  font-size: 25.3379px;
  color: rgb(255, 255, 255);
  line-height: 35px;
  text-transform: none;
  list-style-type: none;
}

.home_main {
  height: 80vh;
  width: 100%;
  display: flex;
  align-items: center;
}

.home_title {
  font-weight: 400;
  font-style: normal;
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-family: "Nunito", sans-serif;
  font-size: 4em;
  line-height: 82px;
  z-index: 99;
}

.desc {
  font-family: "Nunito", sans-serif;
  font-size: .4em;
  color: rgb(23, 38, 39);
  line-height: 35px;
  letter-spacing: 0em;
  text-transform: none;
  list-style-type: none;
  background-color: #20CD8D;
  text-align: center;
  border-radius: 1rem;
  padding: 4px;
  margin-top: 20px;
}



/* website design according to Table & Ipad width */
@media (max-width: 1024px) {
  .home_container {
    padding: 3em;
  }
}


@media (max-width: 913px) {
  .home_container {
    padding: 6em;
  }

  .home_header {
    font-size: 30px;
  }

  .home_title {
    font-size: 6em;
    line-height: 92px;
  }

  .desc {
    padding: 12px;
  }
}


@media (max-width: 769px) {
  .home_container {
    padding: 6em;
  }

  .home_header {
    font-size: 30px;
  }

  .home_title {
    font-size: 5em;
    line-height: 82px;
  }

  .desc {
    padding: 12px;
  }
}

@media (max-width: 695px) {
  .home_container {
    padding: 2em;
  }

  .home_title {
    font-size: 4em;
    line-height: 82px;
  }

  .desc {
    padding: 12px;
  }
}



/* website design according to Mobile width  */
@media (max-width: 415px) {
  .home_container {
    padding: 2em;
  }

  .home_title {
    font-size: 3.4em;
    line-height: 50px;
  }
}

@media (max-width: 394px) {
  .home_container {
    padding: 2em;
  }

  .home_title {
    font-size: 3em;
    line-height: 50px;
  }
}

@media (max-width: 361px) {
  .home_container {
    padding: 2em;
  }

  .home_title {
    font-size: 2.5em;
    line-height: 50px;
  }
}

@media (max-width: 280px) {
  .home_container {
    padding: 1em;
  }

  .home_title {
    font-size: 2.4em;
    line-height: 50px;
  }
}

@media (max-width: 241px) {
  .home_container {
    padding: 1em;
  }

  .home_title {
    font-size: 2em;
    line-height: 35px;
  }
}
